<template>
  <div class="hello">
   <div class="hiragana">
     <li v-for="value in listHiragana">{{value}}</li>
   </div>
   <div class="katakana">
     <li v-for="value in listKatakana">{{value}}</li>
   </div>
   <button class="button" v-on:click="reset">重新开始</button>
   <a class="time" v-if="isShow">{{count}}s</a>
  </div>

</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      count: 0,
      isShow: false,
      listHiragana: ['あ', 'い', 'う', 'え', 'お', 'か', 'き', 'く', 'け', 'こ', 'さ', 'し', 'す', 'せ', 'そ', 'た', 'ち', 'つ', 'て', 'と', 'な', 'に', 'ぬ', 'ね', 'の', 'は', 'ひ', 'ふ', 'へ', 'ほ', 'ま', 'み', 'む', 'め', 'も', 'や', 'ゆ', 'よ', 'ら', 'り', 'る', 'れ', 'ろ', 'わ', 'を'],
      listKatakana: ['ア', 'イ', 'ウ', 'エ', 'オ', 'カ', 'キ', 'ク', 'ケ', 'コ', 'サ', 'シ', 'ス', 'セ', 'ソ', 'タ', 'チ', 'ツ', 'テ', 'ト', 'ナ', 'ニ', 'ヌ', 'ネ', 'ノ', 'ハ', 'ヒ', 'フ', 'ヘ', 'ホ', 'マ', 'ミ', 'ム', 'メ', 'モ', 'ヤ', 'ユ', 'ヨ', 'ラ', 'リ', 'ル', 'レ', 'ロ', 'ワ', 'ヲ']
    }
  },
  mounted() {
    setInterval(() => {
      this.count++
    }, 1000)
  },
  methods: {
    reset: function () {
      this.listHiragana.sort(this.randomsort)
      this.listKatakana.sort(this.randomsort)
      this.verification()
    },
    randomsort: function (a, b) {
      return Math.random() > 0.5 ? -1 : 1
    },
    verification: function () {
      this.isShow = true
      this.count = 0
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 20px;
  font-size: 25px;
  font-weight: bolder;
}
a {
  color: #42b983;
}
.hiragana{
  border: 1px solid;
  margin: 3px 0px 3px 0px;
}
.katakana{
  border: 1px solid;
  margin: 3px 0px 3px 0px;
}
.time{
  font-size: 40px;
  width: 100px;
  height: 35px;
  position: fixed;
  left: 15px;
  bottom: 15px;
}
.button{
  width: 100px;
  height: 35px;
  position: fixed;
  bottom: 15px;
  right: 15px;
}
</style>



